/**
 * domdocument
 */
export function getPosition(dom: HTMLElement) {
  let t = dom.offsetTop,
    l = dom.offsetLeft;
  let obj: HTMLElement = dom;
  //topleft
  while (obj.offsetParent) {
    obj = obj.offsetParent as HTMLElement;
    t += obj.offsetTop;
    l += obj.offsetLeft;
  }
  return {
    left: l,
    top: t,
  };
}
