
import { defineComponent, onMounted, ref } from "vue";
import Sketch from "../sketch";
import { getPosition } from "./utils";

export default defineComponent({
  components: { Sketch },
  props: {
    modelValue: {
      type: String,
      default: "",
    },
  },
  setup(props, context) {
    const model = ref("");
    const visible = ref(false);
    const pickerRef = ref({} as HTMLElement);
    const position = ref(["bottom", "left"]);
    const calcPosition = () => {
      const winW = document.body.clientWidth;
      const winH = document.body.clientHeight;
      position.value = [];
      const p = [];
      if (pickerRef.value) {
        //
        const posi = getPosition(pickerRef.value);
        if (posi.left < 220) {
          p.push("left");
        } else if (posi.left + 30 + 220 > winW) {
          p.push("right");
        } else {
          p.push("left");
        }
        if (posi.top < 350) {
          p.push("bottom");
        } else if (posi.top > winH - 350) {
          p.push("top");
        } else {
          p.push("bottom");
        }
        position.value = p;
      }
    };
    onMounted(() => {
      calcPosition();
    });
    const show = () => {
      visible.value = true;
      calcPosition();
      document.addEventListener("click", () => {
        visible.value = false;
      });
    };
    const cancel = () => {
      model.value = props.modelValue;
      visible.value = false;
    };
    const confirm = () => {
      context.emit("update:modelValue", model.value);
      visible.value = false;
      context.emit("change", model.value);
    };
    return {
      model,
      visible,
      show,
      confirm,
      cancel,
      position,
      pickerRef,
    };
  },
});
